body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.home {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

.home__content {
  width: 90%;
}

@media (min-width: 576px) {
  .home__content {
    width: 80%;
  }
}

@media (min-width: 768px) {
  .home__content {
    width: 70%;
  }
}

@media (min-width: 992px) {
  .home__content {
    width: 60%;
  }
}

@media (min-width: 1200px) {
  .home__content {
    width: 50%;
  }
}

.footer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-align-content: center;
          align-content: center;
  width: 100%;
  margin-top: 50px;
  font-size: 12px;
  text-transform: uppercase;
}

.footer--red {
  color: #E91E63;
}

.footer__line {
  width: 80%;
  background-color: gray;
  height: 1px;
}

.input-text {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-content: center;
          align-content: center;
  text-align: start;
  margin: 10px;
}

.input-text__span {
  margin-bottom: 10px;
  font-size: 18px;
  font-weight: bold;
}

.input-text__textarea {
  min-height: 100px;
  font-size: 18px;
  border-radius: 3px;
}

.input-text__textarea:focus {
  outline: none !important;
  border:1px solid #e74c3c;
  box-shadow: 0 0 10px #e74c3c;
}

.translated-text {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-content: center;
          align-content: center;
  text-align: start;
  margin: 40px 10px 10px 10px;
}

.translated-text__span {
  font-size: 18px;
  font-weight: bold;
}

.translated-lines {
  margin: 10px 0;
}

.translated-lines-item {
  margin: 1px;
}

